<template>
  <div class="fixed top-0 left-0 right-0 w-full md:inset-0 
    md:h-full mx-auto bg-black/50 flex justify-center items-center overlay" v-if="active">
    <div class="relative w-full h-full max-w-4xl p-4 md:h-auto">
      <!-- Modal content -->
      <div class="relative bg-black z-1000">
        <!-- Modal header -->
        <div class="flex items-start justify-between p-1">
          <button type="button" @click.prevent="closeModal"
            class="text-white  hover:scale-150 rounded-lg text-sm p-1.5 ml-auto inline-flex items-center">
            <svg class="w-5 h-5" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
              <path fill-rule="evenodd"
                d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                clip-rule="evenodd"></path>
            </svg>
          </button>
        </div>
        <!-- Modal body -->
        <video class="max-w-full" ref="videoref" controls autoplay :key="video">
          <source :src="source" type="video/mp4" />
        </video>
      </div>
    </div>
  </div>
</template>
<script>
import { ref } from "vue";

export default {
  props: ["active", "toggleModal", "source"],
  setup(props) {
    const videoref = ref(null);
    const closeModal = function () {
      videoref.value.pause();
      videoref.value.currentTime = 0;
      props.toggleModal();
    }

    return {
      closeModal, videoref
    }
  }
}
</script>

<style scoped>
.overlay {
  z-index: 1000;
}
</style>