import { createRouter, createWebHistory } from "vue-router";
import Presentation from "../views/Presentation.vue";

const routes = [
  {
    path: "/",
    name: "Presentation",
    component: Presentation,
  },
  {
    path: "/soin-corps",
    name: "SoinCorps",
    component: () => import("../views/SoinCorps.vue"),
  },
  {
    path: "/soin-visage",
    name: "SoinVisage",
    component: () => import("../views/SoinVisage.vue"),
  },
  {
    path: "/aqua-wellness",
    name: "AquaWellness",
    component: () => import("../views/AquaWellness.vue"),
  },
  {
    path: "/informations",
    name: "Informations",
    component: () => import("../views/Informations.vue"),
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
